.header {
  height: 230px;
  width: 100%;
  background-image: url("header-background-mobile.a8ceff09.jpg");
  background-repeat: no-repeat;
  background-size: 100vw 230px;
  margin-bottom: 20px;
  font-size: 14px;
}

.header__library {
  background-image: url("my-library-mobile.f70bf307.jpg");
}

.header__library-buttons {
  gap: 20px;
}

.header__library-buttons > button {
  cursor: pointer;
  height: 44px;
  text-transform: uppercase;
  width: 130px;
  background: none;
  border: 1px solid #fff;
  border-radius: 5px;
  margin-top: 70px;
}

.header__library-buttons > button:first-of-type {
  margin-right: 15px;
}

.header__library-buttons--active {
  background: #ff6b08 !important;
}

.header__container {
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 40px 20px;
  display: flex;
}

.header__nav {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__nav-title {
  display: none;
}

.header__nav-container {
  width: 280px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__nav-container:hover a svg use {
  fill: #ff6b08;
  transition: all .5s;
}

.header__nav-list {
  align-items: center;
  gap: 40px;
  list-style: none;
  display: flex;
}

.header__nav-list-item > a {
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
}

.header__nav-list-item > a:hover, .header__nav-list-item > a:focus {
  color: #ff6b08;
  transition: all .5s;
}

.header__nav-input {
  color: #fff;
  width: 280px;
  background: none;
  border: none;
  border-bottom: 1px solid;
  outline: none;
  margin-top: 60px;
  padding: 4px 10px;
}

.header__nav-input-submit {
  border: 0;
  position: relative;
}

.header__nav-input--loupe {
  cursor: pointer;
  position: absolute;
  top: -12px;
  left: -12px;
}

.header__error {
  color: #ff001b;
  text-align: center;
  width: 220px;
  margin: 16px auto;
}

@media screen and (min-width: 768px) {
  .header {
    height: 216px;
    background-size: 100vw 216px;
    margin-bottom: 60px;
  }

  .header .header__nav-title {
    color: #fff;
    font-size: 30px;
    transition: all .5s ease-in-out;
    display: inline;
  }

  .header .header__nav-title:hover {
    color: #ff6b08;
  }

  .header .header__nav-container {
    width: 704px;
  }

  .header .header__nav-container > a {
    text-decoration: none;
  }

